<template>
  <div class="main_navs">
    <Header></Header>
      <div v-if="is_up">
        <my-upload field="img"
                   @crop-success="trySubmitFile"
                   v-model="show"
                   :width="300"
                   :height="300"
                   url="/upload"
                   :params="params"
                   :headers="headers"
                   langType="en"
                   img-format="png"></my-upload>
        <img :src="imgDataUrl">
      </div>
    <div class="container">
      <div class="profile_title">
        Profil sozlamalari
      </div>
      <div class="row">
        <div class="col-xl-3">
          <SidebarMain></SidebarMain>
        </div>
        <div class="col-xl-9">
          <div class="settings_prof" :style="{ 'background-image': 'url(' + imageUrl[0] + ')' }">
            <div class="new_profile_img" v-if="imageUrl1.length>0">
              <img :src="imageUrl1[0]" alt="">
            </div>
            <div class="new_profile_img" v-else>

              <svg width="177" height="163" viewBox="0 0 177 163" fill="none" xmlns="http://www.w3.org/2000/svg">
                <circle cx="88.6019" cy="161.603" r="88.0385" fill="#9DB3C8"/>
                <circle cx="88.6027" cy="44.218" r="43.3205" fill="#33587C" fill-opacity="0.8"/>
              </svg>

            </div>
            <label for="change_profile" class="edit_icon">
              <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M7.5 2.5V7.5H2.5V8.5H7.5V13.5H8.5V8.5H13.5V7.5H8.5V2.5H7.5Z" fill="#043936"/>
              </svg>
            </label>
            <div class="prof_data">
              <div>
                <div class="pro_names">{{this.full_name}}</div>
                <div class="prof_mail">{{$store.state.me.email}}</div>
              </div>
              <div>
                <input type="file" id="change_profile" class="d-none"
                       @change="trySubmitFile1"
                       accept="*"
                       name="datafiles1"
                       ref="datafiles1"
                >

                <input type="file" class="form-control d-none"
                       id="info_img1"
                       @change="trySubmitFile"
                       accept="*"
                       name="datafiles"
                       ref="datafiles"
                >
                <label for="info_img1"  class="change_pro_img">{{$t('edt_pro')}}</label>
              </div>
            </div>
          </div>
          <div class="search_data">

            <div class="passport_datass change_input">

              <div class="filt_users row">
                <div class="col-xl-12">
                  <div>
                    <label>JShShIR </label>
                  </div>
                  <div>
                    <input type="number" @input="filtCount()" v-model="jshshr" id="jshshr" placeholder="JShShIR ">
                  </div>
                </div>
                <div class="text-end">
                  <button @click="filtUser()" class="btn btn-primary mt-4">Qidiruv</button>
                </div>
              </div>
            </div>
            <div class="passport_datass change_input">

              <div class="filt_users row">
                <div class="col-xl-4">
                  <div>
                    <label>Passport seriya</label>
                  </div>
                  <div>
                    <input type="text" placeholder="AA" v-model="documentseries">
                  </div>
                </div>
                <div class="col-xl-4">
                  <div>
                    <label>Passport raqam</label>
                  </div>
                  <div>
                    <input type="text" placeholder="1234567" v-model="documentnumber">
                  </div>
                </div>
                <div class="col-xl-4">
                  <div>
                    <label>Tug'ilgan sana</label>
                  </div>
                  <div>
                    <input type="text" placeholder="yyyy-mm-dd" v-model="dateofbirth">
                  </div>
                </div>
                <div class="text-end">
                  <button @click="filtUserP()" class="btn btn-primary mt-4">Qidiruv</button>
                </div>
              </div>
            </div>
          </div>
          <div class="card_profiles_change">
           <div class="container">
             <div class="change_input">
               <div class="row">
                 <div class="col-xl-6">
                   <div>
                     <label>F.I.SH</label>
                   </div>
                   <div>
                     <input type="text" v-model="full_name">
                   </div>
                 </div>
                 <div class="col-xl-6">
                   <div>
                     <label>Tugulgan sanasi</label>
                   </div>
                   <div>
                     <input type="text" placeholder="dd-mm-yyyy"  v-model="date_of_birth">
                   </div>
                 </div>
                 <div class="col-xl-6">
                   <div>
                     <label>Viloyat</label>
                   </div>
                   <div>
                     <input type="text" v-model="region_name">
                   </div>
                 </div>
                 <div class="col-xl-6">
                   <div>
                     <label>Tuman</label>
                   </div>
                   <div>
                     <input type="text" v-model="district_name">
                   </div>
                 </div>
                 <div class="col-xl-6">
                   <div>
                     <label>Maktab nomi</label>
                   </div>
                   <div>
                     <input type="text" v-model="school_name">
                   </div>
                 </div>
                 <div class="col-xl-6">
                   <div>
                     <label>Sinif nomi</label>
                   </div>
                   <div>
                     <input type="text" v-model="class_name">
                   </div>
                 </div>
                 <div class="col-xl-6">
                   <div>
                     <label>Qiziqishlar</label>
                   </div>
                   <div>
                     <input type="text" v-model="interests">
                   </div>
                 </div>
               </div>
             </div>
             <div class="change_input mt-4"  v-if="$store.state.me">
               <div class="row">
                 <div>
                   <label>E-mail</label>
                 </div>
                 <div>
                   <input type="email" v-model="$store.state.me.email">
                 </div>
               </div>
             </div>
           </div>
            <div class="button_main_p">
              <button class="remove">Bekor qilish</button>
              <button class="save" @click="putUsers()"> O'zgarishlarni saqlash</button>
            </div>
          </div>
          <div class="card_profiles_change passport_datass change_input">
            <form @submit.prevent="changePasword()">
              <div class="filt_users row">
                <div class="col-xl-4">
                  <div>
                    <label>Eski parol</label>
                  </div>
                  <div>
                    <input type="text" v-model="change_password" placeholder="Iltimos, eski parolni kiriting" >
                  </div>
                </div>
                <div class="col-xl-4">
                  <div>
                    <label>Yangi parol</label>
                  </div>
                  <div>
                    <input type="text" v-model="change_confirm_pass" placeholder="Iltimos, yangi parolni kiriting" >
                  </div>
                </div>
                <div class="col-xl-4">
                  <div>
                    <label>Telefon raqam yoki email</label>
                  </div>
                  <div>
                    <input class="phone_or_mail" v-model="change_mail" type="text" placeholder="Iltimos, yangi parolni kiriting" >
                  </div>
                </div>
                <div class="text-end mt-4">
                  <button  class="btn btn-primary ">Saqlash</button>
                </div>
              </div>

            </form>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";
import SidebarMain from "../../../components/SidebarMain";
import myUpload from 'vue-image-crop-upload';
export default {
  components: {SidebarMain, Footer, Header,'my-upload': myUpload},
  data(){
    return{
      change_mail:'',
      headers: {
        smail: '*_~'
      },
      is_up:false,
      imgDataUrl: '',
      change_password:'',
      change_confirm_pass:'',
      jshshr:'',
      class_name:'',
      school_name:'',
      school_id:'',
      district_name:'',
      district_id:'',
      gender_name:'',
      date_of_birth:'',
      full_name:'',
      region_id:'',
      region_name:'',
      gender_id:'',
      class_id:'',

      imageUrl:[],
      myImages:[],
      files_name:[],
      imageUrl1:[],
      myImages1:[],
      files_name1:[],
      interests:'',
      profile_cover:'',
      profile_image:'',
      documentnumber:'',
      documentseries:'',
      dateofbirth:''
    }
  },
   mounted() {
     this.$axiosDefault.get(`students/me/`,
         {
           headers: {
             'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
             'Accept-Language':'oz'
           }
         }
     )
         .then(res=>{
           this.$axiosDefault.get(`students/${res.data.id}/profile-settings`,{
             headers:{
               'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
             }
           })
               .then(res=>{
                    this.class_name=res.data.class_name,
                    this.school_name=res.data.school_name,
                    this.school_id=res.data.school_id,
                    this.district_name=res.data.district_name,
                    this.district_id=res.data.district_id
                    this.gender_name=res.data.gender_name,
                    this.gender=res.data.gender
                    this.date_of_birth=new Date(res.data.date_of_birth).toJSON().slice(0,10).split('-').reverse().join('-'),
                    this.full_name=res.data.full_name
                    this.region_id=res.data.region_id
                    this.region_name=res.data.region_name
                    this.class_id=res.data.class_id
                    this.profile_image=res.data.profile_image_url
                    this.profile_cover=res.data.profile_cover_url
                    this.interests=res.data.interests
                  this.imageUrl1.push(res.data.profile_image_url)
                 this.imageUrl.push(res.data.profile_cover_url)
                 console.log(this.imageUrl)
               })

         })

  },
  methods:{

    cropSuccess(imgDataUrl, field){
      console.log(field,"hello");
      this.imgDataUrl = imgDataUrl;
    },
    /**
     * upload success
     *
     * [param] jsonData  server api return data, already json encode
     * [param] field
     */
    cropUploadSuccess(jsonData, field){
      console.log('-------- upload success --------');
      console.log(jsonData);
      console.log('field: ' + field);
    },
    /**
     * upload fail
     *
     * [param] status    server api return error status, like 500
     * [param] field
     */
    cropUploadFail(status, field){
      console.log('-------- upload fail --------');
      console.log(status);
      console.log('field: ' + field);
      console.log("dskajdnsak11")
    },

    filtCount(){
      if(this.jshshr>999999999999){
        document.getElementById('jshshr').style.border="1px solid red";


      }
    },
    changePasword(){
      const form = new FormData();
      form.append("password", this.change_password);
      form.append("confirm_password", this.change_confirm_pass);
      form.append("by_phone_number", this.change_mail);
      this.$axiosDefault.post('students/reset-password/',form)
          .then(()=>{
            localStorage.setItem('reg_token',this.$route.query.token)
          })
          .catch(error=>{
            console.log(error.message)

            this.$toast(`${error.message}`, {
              timeout: 4000,
              type:"error"
            });
          })
    },
    trySubmitFile(e) {
      // console.log(e,"hdjahbdjhsa")
      // this.imageUrl=e
      // this.myImages.push(e)
      // this.is_up=false

      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      console.log(files,"dnskajn")
      files.forEach((file) => {
        this.myImages.push(file);
        console.log(this.myImages[0],"sdnsakj")
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      this.imageUrl1=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      console.log(files,"dnskajn")
      files.forEach((file) => {
        this.myImages1.push(file);
        console.log(this.myImages1[0],"sdnsakj")
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    filtUser(){
      this.$axiosDefault.get('integration/student',{
        params:{
          'pinf':this.jshshr,
          'token':localStorage.getItem('reg_token')

        },
      })
      .then(res=>{
            this.class_name=res.data.class_name,
            this.school_name=res.data.school_name,
            this.school_id=res.data.school_id,
            this.district_name=res.data.district_name,
            this.district_id=res.data.district_id
            this.gender_name=res.data.gender_name,
            this.gender=res.data.gender_id
            this.date_of_birth=new Date(res.data.date_of_birth).toJSON().slice(0,10).split('-').reverse().join('-'),
            this.full_name=res.data.full_name
            this.region_id=res.data.region_id
            this.region_name=res.data.region_name
            this.class_id=res.data.class_id
      })
    },
    filtUserP(){
      this.$axiosDefault.get('integration/student',{
        params:{

          'documentseries':this.documentseries,
          'documentnumber':this.documentnumber,
          'dateofbirth':this.dateofbirth,
          'token':localStorage.getItem('reg_token')
        },
      })
          .then(res=>{
            this.class_name=res.data.class_name,
                this.school_name=res.data.school_name,
                this.school_id=res.data.school_id,
                this.district_name=res.data.district_name,
                this.district_id=res.data.district_id
            this.gender_name=res.data.gender_name,
                this.gender=res.data.gender_id
            this.date_of_birth=new Date(res.data.date_of_birth).toJSON().slice(0,10).split('-').reverse().join('-'),
                this.full_name=res.data.full_name
            this.region_id=res.data.region_id
            this.region_name=res.data.region_name
            this.class_id=res.data.class_id
          })
    },
    putUsers(){
      const form = new FormData();
      form.append("profile_image", this.myImages1[0]);
      form.append("profile_cover", this.myImages[0]);
      form.append("full_name", this.full_name);
      form.append("date_of_birth", this.date_of_birth);
      form.append("gender", this.gender);
      form.append("interests", this.interests);
      form.append("region_id", this.region_id);
      form.append("region_name", this.region_name);
      form.append("district_id", this.district_id);
      form.append("district_name", this.district_name);
      form.append("school_id", this.school_id);
      form.append("school_name", this.school_name);
      form.append("class_id", this.class_id);
      form.append("class_name", this.class_name);
      this.$axiosDefault.put(`students/${this.$store.state.me.id}/profile-settings/`,form,
  {
    headers:{
      'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
    }
  })
      .then(()=>{
        this.$axiosDefault.get(`students/me/`,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                'Accept-Language':'oz'
              }
            }
        )
            .then(res=>{
              this.$axiosDefault.get(`students/${res.data.id}/profile-settings`,{
                headers:{
                  'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                }
              })
                  .then(res=>{
                    this.$toast("Ma'lumot yangilandi", {
                      timeout: 10000,
                      type:"success"
                    });
                    this.class_name=res.data.class_name,
                        this.school_name=res.data.school_name,
                        this.school_id=res.data.school_id,
                        this.district_name=res.data.district_name,
                        this.district_id=res.data.district_id
                    this.gender_name=res.data.gender_name,
                        this.gender=res.data.gender
                    this.date_of_birth=new Date(res.data.date_of_birth).toJSON().slice(0,10).split('-').reverse().join('-'),
                        this.full_name=res.data.full_name
                    this.region_id=res.data.region_id
                    this.region_name=res.data.region_name
                    this.class_id=res.data.class_id
                    this.profile_image=res.data.profile_image_url
                    this.profile_cover=res.data.profile_cover_url
                    this.interests=res.data.interests
                  })

            })

      })
      .catch(error=>{
        console.log(error.response.data)
        if(error.response.data.profile_image){

          this.$toast(`${error.response.data.profile_image}`, {
            timeout: 4000,
            type:"error"
          });
        }
        if(error.response.data.profile_cover){
          this.$toast(`${error.response.data.profile_cover}`, {
            timeout: 4000,
            type:"error"
          });
        }

        if(error.response.data.date_of_birth){

          this.$toast(`${error.response.data.date_of_birth}`, {
            timeout: 4000,
            type:"error"
          });

        }
              })
    }
  }
}
</script>
